.banner {
    color: #fff;
    object-fit: contain;
    height: 448px;
}
.banner h1 {
    text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
}
.banner__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__desc {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 200ms;
}

.banner--fadeBottom {
    position: relative;
    top: 140px;
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #141414
    );
}

@media (max-width: 800px) {
    .banner__title {
        font-size: 40px;
    }
}
@media (max-width: 500px) {
    .banner__title {
        font-size: 36px;
    }
    .banner__desc {
        width: 250px;
    }
}
